body {
  margin: 0;
  box-sizing: border-box;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(to right, rgba(198, 255, 221, 0.7), rgba(251, 215, 134, 0.7), rgba(247, 121, 125, 0.7));
  flex-flow: column;
  font-family: 'Open Sans', sans-serif; }

h1 {
  font: 700 48px/1.2 "Pacifico" , cursive; }

h2 {
  font: 700 32px/1.2 "Pacifico", cursive; }

h3 {
  font: 700 24px/1.2 "Pacifico", cursive; }

h4 {
  font: 700 20px/1.2 "Pacifico", cursive; }

h1, h2, h3, h4 {
  margin: 0;
  margin-bottom: 10px; }

h5 {
  font: 500 18px/1.2 "Pacifico", cursive;
  margin-bottom: 10px; }

h6 {
  font: 500 16px/1.2 "Pacifico", cursive;
  text-transform: uppercase; }

p {
  font: 400 18px 'Open Sans', sans-serif;
  margin-bottom: 0; }

.navbar {
  background-color: #ECB22E;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  /* bottom:0 */
  height: 60px;
  width: 100%;
  z-index: 1; }
  .navbar .nav-content {
    display: flex;
    align-items: center; }
    .navbar .nav-content .nav-img {
      height: 40px;
      /* border: solid 2px gray */
      border-radius: 50%;
      background-color: #fff; }
    .navbar .nav-content .nav-title {
      margin-left: 10px;
      font-family: 'Roboto', sans-serif;
      font-size: 30px;
      color: white; }

/* The Modal (background) */
.modal {
  display: flex;
  /* block by default */
  justify-content: center;
  align-items: center;
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */ }

/* Modal Content */
.modal-content {
  background-color: #fff;
  margin: auto;
  padding: 2vh;
  width: 70vh;
  height: 70vh;
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 0 16px 32px 0 rgba(138, 31, 138, 0.37);
  backdrop-filter: blur(10px);
  border-radius: 3vh;
  border: 3px solid rgba(255, 255, 255, 0.25); }

/* The Close Button */
.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold; }

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer; }

.slideshow-container {
  height: 90%;
  position: relative;
  margin: auto;
  padding: 2vh;
  /* Hide the images by default */
  /* Fading animation */ }
  .slideshow-container .mySlides {
    display: none; }

.fade {
  animation-name: fade;
  animation-duration: 1.5s; }

@keyframes fade {
  from {
    opacity: .4; }
  to {
    opacity: 1; } }

.dot-container {
  height: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* The dots/bullets/indicators */ }
  .dot-container .dot {
    cursor: pointer;
    height: 15px;
    width: 15px;
    margin: 0 2px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease; }
  .dot-container .active, .dot-container .dot:hover {
    background-color: #717171; }

.chess {
  margin-left: 15px;
  margin-right: 15px; }

.players-row {
  display: flex;
  justify-content: space-between;
  margin-top: 5vh;
  height: 20vh;
  width: 70vw; }

.player-box {
  height: 100%;
  width: 30%; }

.headbar {
  display: flex;
  justify-content: space-between;
  margin: 0 15px 15px 15px; }

#app {
  /* width:40rem */
  /* height: 50vh */
  padding: 2vh;
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 0 16px 32px 0 rgba(138, 31, 138, 0.37);
  backdrop-filter: blur(10px);
  border-radius: 3vh;
  border: 3px solid rgba(255, 255, 255, 0.25);
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 1fr);
  width: 70vh;
  height: 70vh; }

.chess-box {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; }
  .chess-box .chess-piece {
    transition: 1s;
    width: 80%;
    height: auto; }
  .chess-box.selected {
    box-shadow: inset 0 0 30px #311B0B; }
  .chess-box.possible-move {
    box-shadow: inset 0 0 30px #0f0; }
  .chess-box.possible-target {
    box-shadow: inset 0 0 30px yellow; }
  .chess-box.checker {
    box-shadow: inset 0 0 30px blue; }
  .chess-box.checked-white {
    box-shadow: inset 0 0 30px red; }
  .chess-box.checked-black {
    box-shadow: inset 0 0 30px red; }
  .chess-box.castling {
    box-shadow: inset 0 0 30px #0f0; }

.white {
  background-position: center;
  background-size: cover;
  color: black; }

.black {
  background-color: #b58863;
  background-position: center;
  background-size: cover;
  /* background-image: url('../../../assets/white-marble.jpg') */
  filter: brightness(1.1);
  /* background-image: url('../../../assets/green-marble.jpg') */
  /* background-image: url('../../../assets/dark-wood.jpg') */ }

